import React from 'react';

export default class InputScanner extends React.Component{
    constructor(){
        super();

        this.state = {
            barcode: ''
        }
    }

    handleInputChange = (e) => {
        this.setState({barcode: e.target.value});
        this.props.handleUpdateState({barcode: e.target.value}, false);
    }

    handleFormSubmit = (e) => {
        e.preventDefault();
        console.log(this.state.barcode);
        this.props.handleUpdateState({barcode: this.state.barcode, step: 2}, true);
    }

    render(){
        return(
            <div className="manual-area-input">
                <label className="font-weight-bold mb-0" htmlFor="barcode">Enter barcode manually</label>
                <form className="form-inline" onSubmit={this.handleFormSubmit.bind(this)}>
                    <input type="text" id="barcode" className="form-control flex-grow-1 mr-0 mr-sm-3" onChange={this.handleInputChange} value={this.props.value} />
                    <button className="btn btn-outline-secondary d-block d-sm-inline flex-grow-1 flex-sm-grow-0 mt-2 mt-sm-0">Submit</button>
                </form>
            </div>
        )
    }
}