import React from 'react';
import {withRouter} from 'react-router-dom';

class NavItem extends React.Component{
    constructor(){
        super();

        this.state = {
            expanded: false,
            active: false
        }
    }

    componentDidMount = () => {
        let expanded = false;

        React.Children.forEach(this.props.children, child => {
            if(this.props.location.pathname === child.props.to){
                expanded = true;
                return;
            }
        });

        this.setState({expanded: expanded})
    }

    resolveClassName = (path) => {
        return this.props.location.pathname === path ? 'active' : '';
    }

    handleCategoryClick = () => {
        this.setState({expanded: !this.state.expanded})
    }

    render(){
        return(
            <li className={"sidebar-menu--children" + (this.state.expanded ? ' expanded' : '')}>
                <a href="#test" className={this.props.className} onClick={this.handleCategoryClick}>{this.props.text}</a>
                <ul className="list-unstyled">
                    {
                        React.Children.map(this.props.children, (child) => {
                            return <li className={this.resolveClassName(child.props.to)}>{child}</li>
                        })
                    }
                </ul>
            </li>
        )
    }
}

export default withRouter(NavItem);