import React from 'react';
import { BrowserBarcodeReader } from '@zxing/library';
import poster from '../../styles/img/barcode-scanner.png';

export default class CameraScanner extends React.Component{
    constructor(){
        super();

        this.state = {
            deviceId: ''
        }
    }

    codeReader = new BrowserBarcodeReader();

    componentDidMount = () => {
        this.codeReader.getVideoInputDevices()
            .then(videoInputDevices => {
                if(videoInputDevices.length){
                    this.setState({deviceId: videoInputDevices[0].deviceId});
                }
            })
    }

    handleScanEvent = (e) => {
        this.codeReader.decodeOnceFromVideoDevice(this.state.deviceId, 'video')
            .then(result => {
                this.props.showMessage('Code: ' + result.text, 'success');
                this.props.handleUpdateState({barcode: result.text, step: 2}, true);
                this.codeReader.stopStreams();
            }).catch((err) => {
                this.props.showMessage(err, 'error');
            })
    }

    render(){
        return(
            <div className="scanner-area rounded-lg overflow-hidden m-auto">
                <video height="300" id="video" className="rounded-lg w-100" poster={poster}></video>
                <button className="btn btn-success btn-block rounded-0" onClick={this.handleScanEvent}>Scan Now</button>
            </div>  
        )
    }
}