import React from 'react';

import imtu from '../../styles/img/br-mtu-intl.svg';
import igift from '../../styles/img/br-gift-intl.svg';
import {NavLink} from 'react-router-dom';
import NavItem from './NavItem';

export default class Sidebar extends React.Component {
    render() {
        return (
            <nav className="sidebar">
                <div className="sidebar-menu">
                    <ul className="list-unstyled">
                        <li>
                            <NavLink to="/" exact className="text-uppercase">Home</NavLink>
                        </li>
                        <li>
                            <NavLink to="/scanner" className="text-uppercase">Activate hard card</NavLink>
                        </li>
                        <NavItem className="text-uppercase" text="Products">
                            <NavLink to="/imtu" className="text-uppercase" href="/">
                                <span className="sidebar-menu--icon">
                                    <img src={imtu} alt="IMTU" />
                                </span>
                                <span>INTERNATIONAL MOBILE RECHARGES</span>
                            </NavLink>
                            <NavLink to="/igift" className="text-uppercase" href="/">
                                <span className="sidebar-menu--icon">
                                    <img src={igift} alt="INTERNATIONAL E-GIFT" />
                                </span>
                                <span>INTERNATIONAL E-GIFT</span>
                            </NavLink>
                        </NavItem>
                        <li className="sidebar-menu--children">
                            <a href="/" className="text-uppercase">Reports</a>
                        </li>
                        <li className="sidebar-menu--children">
                            <a href="/" className="text-uppercase">Tools</a>
                        </li>
                        <li className="sidebar-menu--children">
                            <a href="/" className="text-uppercase">BR University</a>
                        </li>
                        <li>
                            <a href="/" className="text-uppercase">About</a>
                        </li>
                    </ul>
                </div>
            </nav>
        )
    }
}