import React from 'react';
import logo from '../../styles/img/logo.svg';
import menu from '../../styles/img/br-menu.svg';
import logOut from '../../styles/img/br-log-out.svg';

export default class Navbar extends React.Component{
    constructor(){
        super();

        this.state = {
            sidebarIsClosed: false
        }
    }

    componentDidMount = () => {
        this.updateWindowWidth();
    }

    changeSidebarState = () => {
        this.setState({sidebarIsClosed: !this.state.sidebarIsClosed});
    }

    updateWindowWidth = () => {
        this.setState({
            sidebarIsClosed: window.innerWidth < 992
        })
    }

    render(){
        return(
            <nav className={"navbar sticky-top bg-primary-dark p-0 justify-content-start align-items-stretch " + (this.state.sidebarIsClosed ? "sidebar-is-closed" : "")}>
                <button className="sidebar-toggler border-0" onClick={this.changeSidebarState}>
                    <img src={menu} alt="menu"></img>
                </button>
                <a className="navbar-brand p-0 bg-primary d-none d-lg-block" href="/">
                    <img className="logo" src={logo} alt="Retailer Portal"></img>
                </a>
                <a className="retailer-info text-white text-decoration-none d-none d-lg-inline-flex flex-column justify-content-center px-3" href="/">
                    <small className="d-block text-capitalize">Account Number</small>
                    <strong className="d-block">1149823084</strong>
                </a>
                <a className="retailer-info text-white text-decoration-none d-none d-lg-inline-flex flex-column justify-content-center px-3" href="/">
                    <small className="d-block text-capitalize">Store Name</small>
                    <strong className="d-block">Amazing Store</strong>
                </a>
                <a className="retailer-info text-white text-decoration-none d-none d-lg-inline-flex flex-column justify-content-center px-3" href="/">
                    <small className="d-block text-capitalize">My IP Address</small>
                    <strong className="d-block">93.12.54.213</strong>
                </a>
                <div className="flex-grow-1 d-inline-flex justify-content-end">
                    <a className="log-out retailer-info text-white text-uppercase text-decoration-none px-3 mr-2 d-flex align-items-center" href="/">
                        <strong className="d-none d-lg-inline">Log out</strong>
                        <img src={logOut} className="h-50 ml-2" alt="Log out"></img>
                    </a>
                </div>
            </nav>
        )
    }
}