import React from 'react';
import { BrowserBarcodeReader } from '@zxing/library';

export default class FileScanner extends React.Component{
    constructor(){
        super();

        this.state = {
            barcode: ''
        }
    }

    codeReader = new BrowserBarcodeReader();

    handleFileChange = (event) => {
        if(event.target.files && event.target.files.length){
            let url = URL.createObjectURL(event.target.files[0]);

            this.codeReader.decodeFromImageUrl(url)
            .then(result => {
                console.log(result);
                this.props.showMessage('Code: ' + result.text, 'success');
                this.props.handleUpdateState({barcode: result.text, step: 2}, true);
            })
            .catch(err => {
                console.log(err);
                this.props.showMessage("Can't find any barcode. Please choose another image", 'error');
            })
        }
    }

    render(){
        return(
            <div className="manual-area-file text-center pt-0 pt-md-3 pb-4 pb-md-3 rounded-lg">
                <img src="/icons/img-thumb.svg" alt='Drag' className="d-none d-md-inline mr-2" />
                <span className="d-none d-md-inline">Drag and drop here to scan barcode</span>
                <small className="text-muted mt-1 d-none d-md-block">files supported: JPG, PNG, resolution not less than 1000px</small>
                <label className="btn btn-outline-secondary d-flex d-md-inline-flex align-items-center justify-content-center d-block-sm mt-2" htmlFor="img-file">
                    <input id="img-file" type="file" className="d-none" onChange={this.handleFileChange.bind(this)} />
                    Choose file
                </label>
            </div>
        )
    }
}